.section9 {
  background-color: #1a1a1a;
  padding-top: 120px;
  padding-bottom: 120px;
  .container {
    .row {
      .main {
        width: 100%;
        .titleBox {
          .titleCard {
            font-family: "SF Pro Display";
            width: 100%;
            padding-bottom: 40px;
            max-width: 410px;
            font-style: normal;
            font-weight: 510;
            font-size: 40px;
            line-height: 110%;
            color: #ffffff;
          }
        }
        .aboutTeacher {
          display: grid;
          grid-template-columns: 790px 380px;
          column-gap: 30px;
          .left {
            img {
              width: 100%;
              max-width: 790px;
              border: 1px solid #444444;
            }
          }
          .leftTablet {
            display: none;
          }
          .right {
            position: relative;
            .line {
              width: 100%;
              height: 3px;
              background-color: #ffbd39;
              position: absolute;
              top: 0;
              left: 0;
              &::before {
                content: "";
                width: 100%;
                height: 3px;
                background-color: #ffbd39;
                position: absolute;
                top: 0;
                left: 0;
                transition: all 0.6s;
                transform: scale(0);
                transform-origin: left;
              }
            }
            width: 100%;
            max-width: 380px;
            padding: 30px;
            background-color: #272727;
            .titleText {
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 510;
              font-size: 20px;
              line-height: 130%;
              color: #ffffff;
            }
            ul {
              margin-left: 30px;
              padding-top: 20px;
              padding-bottom: 30px;
              li {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 140%;
                color: #b7b7b7;
              }
              .nextLi {
                margin-top: 20px;
              }
            }
            .registration {
              cursor: pointer;
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 400;
              font-size: 18px;
              line-height: 21px;
              text-decoration-line: underline;
              color: #ffbd39;
            }
          }
          // .right:hover .line::before {
          //   background-color: #ffbd39;
          //   transform: scale(1);
          // }
        }
      }
    }
  }
}

@media only screen and (min-width: 740px) and (max-width: 1199px) {
  .section9 {
    background-color: #1a1a1a;
    padding-top: 80px;
    padding-bottom: 80px;
    .container {
      .row {
        .main {
          width: 100%;
          .titleBox {
            .titleCard {
              width: 100%;
              padding-bottom: 40px;
              max-width: 380px;
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 510;
              font-size: 40px;
              line-height: 110%;
              color: #ffffff;
            }
          }
          .aboutTeacher {
            display: grid;
            grid-template-columns: 360px 360px;
            column-gap: 20px;
            .left {
              display: none;
              img {
                width: 100%;
                max-width: 790px;
                border: 1px solid #444444;
              }
            }
            .leftTablet {
              display: block;
              img {
                width: 360px;
                height: 375px;
              }
            }
          }
        }
      }
    }
  }
}
