.section10 {
  background-color: #212121;
  padding-top: 120px;
  padding-bottom: 120px;
  .container {
    .row {
      .main {
        width: 100%;
        .titleBox {
          .titleCard {
            width: 100%;
            max-width: 490px;
            padding-bottom: 40px;
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 510;
            font-size: 40px;
            line-height: 110%;
            color: #ffffff;
          }
        }
        .registrationBox {
          width: 100%;
          display: grid;
          grid-template-columns: 790px 380px;
          column-gap: 30px;
          .leftBoxes {
            border-top: 1px solid #444444;
            width: 790px;
            display: grid;
            grid-template-columns: 380px 380px;
            column-gap: 30px;

            .left {
              padding-top: 30px;
              display: block;
              .titles {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 510;
                font-size: 20px;
                line-height: 130%;
                color: #ffffff;
              }
              .texts {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 140%;
                color: #b7b7b7;
                width: 100%;

                max-width: 380px;
              }
              .oneTexts {
                margin-top: 20px;
              }
              .twoTexts {
                margin-top: 30px;
                margin-bottom: 30px;
              }
              .threeTexts {
                width: 100%;
                max-width: 370px;
              }
              .sevenTexts {
                color: #ffffff;
                margin-top: 30px;
                span {
                  color: #459bff;
                }
              }
            }
          }
          .right {
            border-top: 3px solid #ffbd39;
            padding: 30px;
            background-color: #272727;
            .title {
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 510;
              font-size: 20px;
              line-height: 130%;
              color: #ffbd39;
            }
            .titles {
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 510;
              font-size: 20px;
              line-height: 130%;
              color: #ffffff;
            }
            .texts {
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 400;
              width: 100%;
              max-width: 380px;
              font-size: 18px;
              line-height: 130%;
              color: #b7b7b7;
            }
            .oneTexts {
              margin-top: 20px;
            }
            .twoTexts {
              margin-top: 30px;
              margin-bottom: 30px;
            }
            .threeTexts {
              width: 100%;
              max-width: 370px;
            }
            .sevenTexts {
              color: #ffffff;
              margin-top: 30px;
              span {
                color: #459bff;
              }
            }

            ul {
              margin-left: 30px;
              padding-top: 10px;
              padding-bottom: 30px;
              li {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 130%;
                color: #b7b7b7;
              }
              .twoLi {
                margin-top: 10px;
              }
              .nextLi {
                margin: 10px 0;
              }
            }
            .registrationBtn {
              cursor: pointer;
              width: 320px;
              border: none;
              padding: 22px 0;
              margin-left: 0px;
              border: 1px solid #ffbd39;
              outline: none;
              background-color: #ffbd39;
              font-family: "SF Pro Display";
              font-style: normal;
              font-weight: 400;
              font-size: 18px;
              line-height: 21px;
              text-align: center;

              a {
                text-decoration: none;
                color: #1a1a1a;
              }
              &:hover {
                background-color: transparent;
              }
            }
            .registrationBtn:hover .registrationLink {
              color: #ffbd39;
            }
          }
        }
      }
    }
  }
}

