.section7 {
  background-color: #1a1a1a;
  .container {
    .row {
      .main {
        width: 100%;
        padding-top: 120px;
        padding-bottom: 120px;

        .textButton {
          margin-bottom: 40px;
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          .cardTitle {
            width: 100%;
            max-width: 410px;
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 510;
            font-size: 40px;
            line-height: 110%;
            color: #ffffff;
          }
          .buttons {
            display: block;
            position: relative;
            width: 146px;
            height: 24px;
            margin-bottom: 40px;
            .next {
              background-color: transparent;
              border: none;
              cursor: pointer;

              svg {
                position: absolute;
                bottom: 0px;
                right: 0;

                fill: #ffbd39;
              }
            }
            .end {
              path {
                fill: #444444;
              }
            }
            .prev {
              background-color: transparent;
              border: none;
              cursor: pointer;

              svg {
                position: absolute;
                bottom: 0px;

                fill: #ffbd39;
                left: 0;
              }
            }
            .beginning {
              path {
                fill: #444444;
              }
            }
          }
          .text {
            margin-top: 40px;
            margin-bottom: 40px;
            max-width: 600px;
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 130%;
            color: #b7b7b7;
          }
        }
        .slider7 {
          width: 100%;
          position: relative;
          .swiper {
            height: 380px;
            .swiper-slide {
              padding: 0;
              margin: 0;
              width: 380px;
              padding: 0px;
              background-color: #212121;
              text-align: start;
              .slideTitle {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 590;
                font-size: 24px;
                line-height: 100%;
                color: #ffffff;
                margin-bottom: 10px;
              }
              .slideText {
                font-family: "SF Pro Display";
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 140%;
                color: #b7b7b7;
              }
            }
          }
        }
      }
    }
  }
}

